<template lang="pug">
include ../../../mixins/bemto/bemto
+b.button-type1(@click="clickHandler" :class='computedClasses' :disabled="disabled")
  button(:type="type" ) {{ title }}
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default() {
        return 'OK'
      },
    },
    type: {
      type: String,
      default() {
        return 'button'
      },
    },
    color: {
      type: String,
      default() {
        return ''
      },
    },
    size: {
      type: String,
      default() {
        return ''
      },
    },
    disabled: {
      type: Boolean,
      default() {
        return false
      },
    },
  },
  methods: {
    clickHandler() {
      this.$emit('click-handler')
    },
  },
  computed: {
    computedClasses() {
      return `button-type1--${this.color} button-type1--${this.size}`
    },
  },
}
</script>
